import React, { useState, useCallback } from 'react';
import "./style.css";
import { Button, IconButton } from "@commons/button";
import { TableProps, additionalButtonsType } from "./interface";
import { useTranslation } from 'react-i18next';
import {
    Table as ChakraTable,
    Thead,
    Tbody,
    Tfoot,
    Box,
    Tr,
    Th,
    Td,
    TableContainer,
} from '@chakra-ui/react'
import Flex from "@commons/flex";


const Table = ({
    dataSource,
    height = "300px",
    rowKey = "rowKey",
    columns,
    hideTools = true,
    canEdit = false,
    canAdd = false,
    canDelete = false,
    onAdd,
    onEdit,
    onDelete,
    actionColumn = false,
    onAction,
    actionLabel = "",
    onSelectedRow,
    canPrint = false,
    onPrint,
    additionalButtons,
    onSave,
    canSave = false,
    width = "100%",
    margin = "",
    padding = ""
}: TableProps) => {
    const { t } = useTranslation()
    const [rowSelected, setRowSelected] = useState()
    const handleSelectedRow = useCallback((item: any) => () => {
        onSelectedRow(item)
        setRowSelected(item)
    }, [onSelectedRow])

    return (
        <>
            <TableContainer width={width} padding={padding} margin={margin}>
                <Flex
                    width='100%'
                    padding='0'
                    margin='5px 0'
                    justifyContent='center'
                    hidden={hideTools}
                >
                    <IconButton
                        icon='fa-sharp fa-solid fa-plus'
                        onClick={onAdd}
                        hidden={!canAdd}
                    />
                    <IconButton
                        icon='fa-sharp fa-solid fa-pen-clip'
                        onClick={onEdit}
                        hidden={!canEdit}
                        disabled={!rowSelected}
                    />
                    <IconButton
                        icon='fa-sharp fa-solid fa-trash'
                        onClick={onDelete}
                        hidden={!canDelete}
                        disabled={!rowSelected}
                    />
                    {additionalButtons && additionalButtons.map((button: additionalButtonsType) => {
                        return (
                            <IconButton
                                icon={button.icon}
                                onClick={button.onClick}
                                disabled={button.disabled}
                                hidden={false}
                            />
                        )
                    })}
                    <IconButton
                        icon='fa-solid fa-floppy-disk'
                        onClick={onSave}
                        hidden={!canSave}
                        disabled={!canSave}
                    />
                    <IconButton
                        icon='fa-solid fa-print'
                        onClick={onPrint}
                        hidden={!canPrint}
                        disabled={!canPrint}
                    />
                </Flex>
                <Box overflowY="auto" height={height}>
                    <ChakraTable>
                        <Thead top={0} position="sticky" bgColor="#3edae6">
                            <Tr>
                                {columns.map((item: any) => {
                                    return (
                                        <Th width={item.width}>
                                            {t(item.title)}
                                        </Th>
                                    )
                                })}
                                <Th hidden={!actionColumn}>{t("actn")}</Th>
                            </Tr>
                        </Thead>
                        <Tbody>
                            {dataSource?.map((item: any) => {
                                return (
                                    <Tr key={item[rowKey]} onClick={handleSelectedRow(item)} background={`${rowSelected === item ? "#dbffbf" : ""}`} >
                                        {columns.map((column: any) => {
                                            return (
                                                <Td width={column.width}>
                                                    {item[column.dataIndex]}
                                                </Td>
                                            )
                                        })}
                                        <Td hidden={!actionColumn}>
                                            <Button
                                                label={actionLabel}
                                                onClick={() => { onAction(item) }}
                                            />
                                        </Td>
                                    </Tr>
                                )
                            })}
                        </Tbody>
                        {/* <Tfoot>
                        <Tr>
                        <Th>To convert</Th>
                        <Th>into</Th>
                        <Th isNumeric>multiply by</Th>
                        </Tr>
                    </Tfoot> */}
                    </ChakraTable>
                </Box>
            </TableContainer>
        </>
    )
}

export default Table;